body,
html {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(/animation-cra/static/media/bgSC.822ff06f.png);
  background-position: 0px 0px;
  font-family: sans-serif;
  -webkit-animation: animatedBackground 12s linear infinite;
          animation: animatedBackground 12s linear infinite;
  background-repeat: repeat;
  background-clip: padding-box;
  background-size: cover;

}


.glass {
  margin: 0 auto;
  height: 100px;
  width: 70vw;
  box-shadow: 0px 0px 20px 11px rgba(255, 255, 255, 0.287);
  background-color: rgba(255, 255, 255, 0.287);
  /* background-color: black; */
  border-radius: 25px;
  display:flex; 
  justify-content: center;
  text-align: justify;
  letter-spacing: normal;

}


.fancy {
  background-image: url(/animation-cra/static/media/teal.ad33d899.png);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  font-size: 6.5rem;
  font-family: 'Orbitron', sans-serif;
  text-transform: uppercase;
  /* letter-spacing: 1rem; */
  margin: 0 auto;
  opacity: 0;
  line-height: 130px;
  width: 70%;
  text-align: justify;
  letter-spacing: 25px;
  /* animation-iteration-count: 2,0, infinite; */
  -webkit-text-stroke-width: 2px;
   -webkit-text-stroke-color: black;

}

